* {
    box-sizing: border-box;
  }
  .menu {
    float:left;
    width:20%;
    text-align:left;
    font-size: 10px;
  }
  .menu a {
    background-color:#e5e5e5;
    padding:8px;
    margin-top:7px;
    display:block;
    width:100%;
    color:black;
  }
  .main {
    float:left;
    width:60%;
    padding:0 20px;
    text-align:center;
  }
  .right {

    float:left;
    width:20%;
    padding:15px;
    margin-top:7px;
    text-align:center;
  }

  .loginSection{
    margin-top:7px;
    text-size-adjust: 10px;
  }
  .loginForm{
    text-size-adjust: 10px;
  }

  .registerSection{
    margin-top:7px;
    text-size-adjust: 10px;
  }
  .btn {
    border: none;
    background-color: inherit;
    padding: 8px 8px;
    font-size: 12px;
    cursor: pointer;
    display: inline-block;
    margin: 2px;
  }
  p {
    margin: 1px;
  }
  .errMsg{
    color: red;
  }
  .btn:hover {background: #eee;}

  .login {color: green; border-radius: 6px;}
  .register {color: red; border-radius: 6px;}
  
  @media only screen and (max-width:620px) {
    /* For mobile phones: */
    .menu, .main, .right {
      width:100%;
    }
  }
  